.meditations-detail {
  .contents {
    &.popup-active {
      filter: blur(5px); } }

  .overview {
    font-family: $roboto-light;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: (25/18);
    color: $white;
    @include mq(ip5) {
      font-size: 16px;
      font-family: $roboto-regular; }
    @include mq(s5) {
      font-size: 16px;
      font-family: $roboto-regular; } }

  .character {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      right: 0; } }

  .stress::before {
    width: 118px;
    height: 105px;
    bottom: 47px;
    background: url('../img/char-stress.png') no-repeat center / cover; }

  .anxiety::before {
    width: 100px;
    height: 100px;
    bottom: 57px;
    background: url('../img/char-anxiety.png') no-repeat center / cover; }

  .creativity::before {
    bottom: 29px;
    width: 80px;
    height: 130px;
    background: url('../img/char-creativity.png') no-repeat center / cover; }

  .focus::before {
    bottom: 61px;
    width: 120px;
    height: 80px;
    background: url('../img/char-focus.png') no-repeat center / cover; }

  .relationships::before {
    bottom: 59px;
    width: 110px;
    height: 137px;
    background: url('../img/char-relationships.png') no-repeat center / cover;
    @include mq(s5) {
      bottom: 61px;
      width: 76px;
      height: 96px; } } }
