.popup-container .popup {
  width: 330px;
  background: $calm;
  border: 2px solid $white;
  border-radius: 5px;
  color: $white;
  @include mq(ip5) {
    width: 290px; } }

.timePickerColon {
  padding-top: 62px !important; }

.timePickerBoxText {
  border: none !important;
  font-size: 28px !important;
  font-family: $roboto-medium;
  letter-spacing: -0.35px; }


.popup-head {
  border: none; }

.popup-title {
  color: $white;
  font-family: $roboto-medium;
  font-size: 20px;
  letter-spacing: -0.4px;
  line-height: (24/20); }

.popup-body {
  padding: 0 18px; }

.popup-head {
  padding: 18px 18px 0; }

.popup-buttons {
  padding: 18px;
  padding-top: 0; }

.button.button-dark.button-clear {
  color: $white;
  display: flex;
  justify-content: center;
  align-items: center; }

.ion-chevron-up:before {
  font-size: 20px;
  line-height: 1; }

.button .icon {
  margin: auto; }

.col {
  padding: 0; }

.row {
  padding: 0; }
