.main {
  position: relative;

  .header {
    padding: 0; }

  .container {
    padding: 20px 20px 10px 20px;
    @include mq(ip5) {
      padding: 15px 15px 5px 15px; } }

  .logo {
    max-width: 100px;
    margin: 10px auto 0 auto; }

  .burger-toggle {
    content: '';
    width: 70px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:active {
      opacity: 0.3; } }

  .burger {
    position: relative;
    width: 30px;
    &, &::before, &::after {
      content: '';
      width: 30px;
      height: 5px;
      background: $white;
      border-radius: 100px; }
    &::before {
      position: absolute;
      top: -10px; }

    &::after {
      position: absolute;
      bottom: -10px; } }

  .lock {
    content: '';
    width: 70px;
    height: 60px;
    background: url('../img/ico-lock.svg') no-repeat center / 25px; }

  .reminder {
    content: '';
    width: 70px;
    height: 60px;
    background: url('../img/ico-noti-off.png') no-repeat center / 25px;
    &.on {
      background: url('../img/ico-noti-on.png') no-repeat center / 25px; } }

  .rounded-icon {
    min-width: 70px;
    max-width: 70px;
    height: 70px;
    margin-right: 15px;
    @include mq(ip5) {
      min-width: 60px;
      max-width: 60px;
      height: 60px;
      margin-right: 10px; } }

  .contents {
    &.menu-active {
      filter: blur(5px); }

    &.popup-active {
      filter: blur(5px); } } }
