.bg-clear {
  background: transparent !important; }

.bg-dark {
  background: rgba(0,0,0,0.8); }

.bg-welzen {
  background: linear-gradient(158deg, #26B4B1 0%, #80699F 49%, #E2387E 100%) no-repeat center / cover; }

.opa-10 {
  opacity: 0.1; }

.opa-20 {
  opacity: 0.2; }

.opa-30 {
  opacity: 0.3; }

.opa-40 {
  opacity: 0.4; }

.opa-50 {
  opacity: 0.5; }

.opa-60 {
  opacity: 0.6; }

.opa-70 {
  opacity: 0.7; }

.opa-80 {
  opacity: 0.8; }

.opa-90 {
  opacity: 0.9; }

.white {
  color: $white; }

.title {
  color: $white;
  font-family: $roboto-light;
  font-size: 32px;
  letter-spacing: -0.64px;
  line-height: (38/32);
  @include mq(s5) {
    font-size: 28px; }
  @include mq(ip5) {
    font-size: 24px; } }

.paragraph {
  font-family: $roboto-light;
  font-size: 18px;
  letter-spacing: -0.36px;
  line-height: (25/18);
  color: $white;
  @include mq(s5) {
    font-size: 16px; } }
