.header {
  font-family: $roboto-regular;
  color: $white;
  border-bottom: 1px solid rgba($white, 0.2);
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
  background: linear-gradient(90deg, #33A8AE, #558CA7);
  padding: 0px;

  &--no-border {
    border-bottom: none; }

  &--transparent {
    background: transparent; }

  .title {
    font-size: 24px;
    letter-spacing: -1px;
    font-family: $roboto-light;
    color: $white;
    margin: 0;
    position: absolute;
    transform-style: preserve-3d;
    top: 50%;
    left: 50%!important;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
    @include mq(s5) {
      font-size: 22px; }
    @include mq(ip5) {
      font-size: 20px; } }


  .header__back-button {
    display: block;
    width: 60px;
    height: 60px;
    margin-right: auto;
    z-index: 2;
    &:active {
      opacity: 0.3; } }

  .header__close-button {
    display: block;
    width: 60px;
    height: 60px;
    margin-left: auto;
    z-index: 2;
    &:active {
      opacity: 0.3; } }


  .header__cta {
    font-family: $roboto-medium;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: 1;
    display: block;
    padding: 10px 9px;
    background: rgba(255, 255, 255, .1);
    border: 1px solid rgba(255, 255, 255, .2);
    color: $white;
    border-radius: 8px;
    opacity: 0;
    margin-left: auto;
    text-decoration: none;
    z-index: 2;
    pointer-events: none;
    margin-right: 10px;
    @include mq(ip5) {
      font-size: 16px; }

    &:active {
      opacity: 0.3;
      pointer-events: all; } } }

