.landing {
  text-align: center;
  color: $white;
  font-size: 20px;
  letter-spacing: -0.4px;
  line-height: 30px;

  @include mq(ip5) {
    font-size: 18px; }

  .sub-title {
    margin-bottom: 5px; }

  .logo {
    max-width: 100px;
    margin: 0px auto; }

  &__title {
    color: $white;
    font-family: $roboto-light;
    font-size: 25px;
    line-height: 35px;
    letter-spacing: -0.56px;
    margin-bottom: 30px;
    margin-top: 0;
    @include mq(s5) {
      font-size: 20px;
      margin-bottom: 20px;
      line-height: 30px; }
    @include mq(ip5) {
      font-size: 20px;
      margin-bottom: 20px;
      line-height: 30px; } }

  .container {
    padding: 20px 20px 10px 20px;
    @include mq(ip5) {
      padding: 0px 15px 15px 15px; } }


  //Slider Settings
  .slider-pager {
    bottom: 0px;
    height: inherit; }

  .ion-record:before {
    color: $white; }

  .slider-slide {
    display: flex;
    flex-direction: column; }

  .title {
    font-size: 25px;
    margin: 0 0 40px 0; }

  .character {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    @include mq(s5) {
      max-width: 280px; }
    @include mq(ip5) {
      max-width: 280px; } } }
