.player {

  &__contents {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; }

  &__background {
    content: '';
    position: relative;
    top: 55%;
    height: 100vh;
    width: 100%;
    background: $pinky;
    background: #CC3366; }

  &__controls--relative {
    position: relative;
    height: 100%; }

  &__controls {
    position: absolute;
    transform-style: preserve-3d;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%); }

  &__play {
    content: '';
    width: 145px;
    height: 145px;
    background: url('../img/btn-play-large.svg') no-repeat center / cover; }

  &__pause {
    content: '';
    width: 145px;
    height: 145px;
    background: url('../img/btn-stop-large.svg') no-repeat center / cover; }

  &__duration {
    display: block;
    font-family: $roboto-regular;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: (25/18);
    color: $white;
    text-align: center;
    margin-top: 25px; } }
