.reminders {
  .row {
    align-items: center;
    justify-content: space-between;
    opacity: 0;
    transition: 0.3s;
    padding: 0;
    pointer-events: none;
    &.shown {
      opacity: 1;
      pointer-events: all; } }

  .time {
    font-family: $roboto-thin;
    font-size: 58px;
    letter-spacing: -1.16px;
    line-height: (68/58);
    color: $white;
    @include mq(ip5) {
      font-size: 45px;
      padding-top: 18px; } } }
