.science {

  .description {
    font-family: $roboto-light;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: (25/18);
    color: $white;
    @include mq(s5) {
      font-size: 16px; }
    @include mq(ip5) {
      font-size: 16px; } }

  .character {
    margin: auto;
    max-width: 330px;
    @include mq(ip5) {
      max-width: 280px; }
    @include mq(s5) {
      max-width: 280px; } }

  //Slider Settings
  .slider {
    height: 520px;
    @include mq(ip5) {
      height: 450px; }
    @include mq(s5) {
      height: 440px; } }
  .slider-pager {
    bottom: 10px; }

  .ion-record:before {
    color: $white; }

  .slider-slide {
    display: flex;
    flex-direction: column; } }
