// Setting stuff for correct footer placement using Chrome Inspector

.footer {
  flex-direction: column;
  height: auto;
  padding: 20px;
  background: linear-gradient(90deg, #A45692, #C84486);
  @include mq(ip5) {
    padding: 15px; }

  &__terms {
    color: rgba($white, 0.5);
    font-family: $roboto-light;
    font-size: 16px;
    letter-spacing: -0.32px;
    line-height: (30/16);
    text-align: center;
    margin-bottom: 5px;
    text-decoration: none;
    @include mq(ip5) {
      font-size: 14px; } } }

.smaller {
  padding: 5px; }

.characters {
  margin-bottom: 35px; }

.bar .title + .button:last-child, .bar > .button + .button:last-child, .bar > .button.pull-right, .bar .buttons.pull-right, .bar .title + .buttons {
  position: initial;
  top: auto;
  right: auto;
  left: auto; }

.button.button-block, .button.button-full {
  margin-bottom: 0; }

.button:after {
  display: none; }
