.navbar__default {
  font-family: $roboto-regular;
  color: $white;
  border-bottom: 1px solid rgba($white, 0.05);
  height: 60px;
  padding-bottom: 0;

  .title {
    font-size: 24px;
    line-height: (28/24);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%; }


  .bar .buttons span {
    display: none; }

  .navbar__back-button {
    color: $white;
    font-size: 24px;
    top: 25%; } }

.has-header {
  top: 60px!important; }

.platform-ios.platform-cordova:not(.fullscreen) .bar-header:not(.bar-subheader) > * {
  margin-top: 0px; }
