.overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,0.5);
  opacity: 0;
  transition: 0.3s;
  pointer-events: none;

  &.menu-active {
    opacity: 1;
    z-index: 2;
    pointer-events: all; } }

.drawer {
  position: fixed;
  width: 300px;
  background: $grape;
  color: $white;
  height: 100vh;
  left: -101%;
  transition: 0.3s 0.15s;
  @include mq(ip5) {
    width: 240px; }


  &.menu-active {
    left: 0;
    z-index: 3; }


  &__container {
    padding: 20px;
    display: flex;
    flex-direction: column;
    height: 100%; }

  &__first-row {
    display: flex;

    .logo {
      margin: 0px auto 0 0; }

    .back-toggle {
      content: '';
      width: 30px;
      height: 22px;
      display: flex;
      align-items: center;
      justify-content: center; }

    .back {
      position: relative;
      width: 27px;
      &, &::before, &::after {
        content: '';
        height: 4px;
        background: $white;
        border-radius: 100px; }
      &::before {
        position: absolute;
        top: -5px;
        width: 15px;
        transform: rotate(-45deg);
        transform-origin: bottom; }

      &::after {
        position: absolute;
        bottom: -5px;
        width: 15px;
        transform: rotate(45deg);
        transform-origin: top; } } }

  &__option {
    display: flex;
    color: $white;
    font-family: $roboto-light;
    font-size: 18px;
    line-height: (21/18);
    border-bottom: 1px solid rgba($white, 0.2);
    padding: 20px 10px;
    text-decoration: none;
    @include mq(ip5) {
      font-size: 14px;
      padding: 15px 10px;
      font-family: $roboto-regular; }

    &:active {
      opacity: 0.3; }

    &--no-border {
      border-bottom: none; }

    .icon {
      width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 20px;
      @include mq(ip5) {
        margin-right: 10px; } }

    .info-box__forward {
      margin-left: auto; } }

  &__cta {
    margin-top: 20px;
    color: $white;
    font-family: $roboto-light;
    font-size: 20px;
    letter-spacing: -0.36px;
    line-height: (21/18);
    display: block;
    text-decoration: none;
    text-align: center;
    padding: 20px;
    background: rgba($white, 0.25);
    border-radius: 8px;
    border: 1px solid rgba($white, 0.5);
    @include mq(ip5) {
      padding: 10px;
      font-size: 16px;
      font-family: $roboto-medium; }

    &:active {
      opacity: 0.3; } } }
