.info-box {
  display: flex;
  padding: 20px 15px;
  border-radius: 5px;
  color: $white;
  margin: 5px 0;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  &:active {
    opacity: 0.3; }

  &__forward {
    min-width: 9px;
    max-width: 9px;
    &--push-left {
      margin-right: 10px; }
    img {
      max-width: inherit; } }

  &__lock {
    min-width: 20px;
    max-width: 20px;
    margin-right: 5px;
    img {
      max-width: inherit; } }

  &__new {
    max-width: 10px; }

  &__updated {}

  &__download {
    min-width: 20px;
    max-width: 20px;
    margin-right: 5px; }

  &__contents {
    margin-right: auto;
    padding-right: 10px;
    flex: 1;

    &--push-right {
      margin-right: auto;
      padding-right: 10px; }

    &--downloads-style {
      @extend .info-box__contents;
      margin-right: auto;
      display: flex;
      align-items: baseline; } }

  &__header-title {
    font-size: 18px;
    font-family: $roboto-light;
    letter-spacing: -0.36px;
    line-height: (21/18);
    display: block;
    @include mq(s5) {
      font-size: 16px;
      font-family: $roboto-regular; }
    @include mq(ip5) {
      font-size: 15px;
      font-family: $roboto-regular; } }


  &__title {
    font-size: 18px;
    font-family: $roboto-regular;
    letter-spacing: -0.36px;
    line-height: 24px;
    display: block;
    @include mq(ip5) {
      font-size: 18px;
      font-family: $roboto-regular; } }

  &__mega-title {
    font-family: $roboto-light;
    font-size: 25px;
    letter-spacing: -0.5px;
    line-height: 1;
    display: block;
    margin: 0 auto;
    @include mq(ip5) {
      font-size: 20px;
      font-family: $roboto-regular; } }

  &__subtitle {
    font-size: 18px;
    font-family: $roboto-light;
    letter-spacing: -0.36px;
    line-height: (25/18);
    display: block;
    @include mq(ip5) {
      font-size: 16px;
      font-family: $roboto-regular; } }

  &__brief {
    font-size: 15px;
    font-family: $roboto-regular;
    letter-spacing: -0.26px;
    line-height: 18px;
    display: block;
    opacity: 0.7;
    @include mq(ip5) {
      font-size: 12.5px;
      font-family: $roboto-regular;
      line-height: 16px; } }

  &__success {
    display: block;
    margin: 0 auto;
    font-family: $roboto-bold;
    font-size: 13px;
    letter-spacing: -0.26px;
    line-height: (18/13); }

  &__status {
    padding: 2px 7px;
    text-transform: uppercase;
    color: $white;
    font-family: $roboto-medium;
    font-size: 13px;
    letter-spacing: -0.26px;
    line-height: (18/13);
    border-radius: 100px;
    border: 3px solid $white;
    margin-left: auto;
    &--current {
      @extend .info-box__status;
      background: $grape; }

    &--upgrade {
      @extend .info-box__status;
      background: $pinky; } }

  &__stats {
    font-family: $roboto-regular;
    font-size: 28px;
    letter-spacing: -0.56px;
    line-height: (28/25);
    display: block;
    margin-left: auto;
    @include mq(ip5) {
      font-size: 22px; } }

  &--purple {
    @extend .info-box;
    background: $grape;
    font-size: 18px;
    padding: 15px 20px;
    //border: 1px solid rgba(255, 255, 255, 0.5)
    margin: 0px 0px 10px 0px;
    @include mq(ip5) {
      padding: 10px 15px; } }

  &--purple-clear {
    @extend .info-box;
    background: rgba($grape, 0.4);
    border: 1px solid rgba($white, 0.4);
    padding: 15px 18px;
    &:active {
      opacity: 1; }

    &--not-top {
      @extend .info-box--purple-clear;
      margin-top: 10px; } }

  &--calm {
    @extend .info-box;
    background: $calm;
    border: 2px solid white;
    font-size: 18px;
    padding: 20px 18px;
    position: relative;
    @include mq(s5) {
      padding: 15px 12px; }
    @include mq(ip5) {
      padding: 10px 9px; }

    &--live-style {
      @extend .info-box--calm;
      padding-right: 7px; }

    &--live-style-pack {
      @extend .info-box--calm;
      padding-right: 0;
      padding: 10px 10px;
      margin: 0 0 20px 0; }

    &--not-top {
      @extend .info-box--calm;
      margin-top: 10px;
      padding: 12px 18px; }

    &--middle {
      @extend .info-box--calm;
      margin-top: 10px;
      margin-bottom: 10px;
      padding: 12px 18px; }

    .tagline {
      position: absolute;
      padding: 5px;
      @include mq(ip5) {
        padding: 1px 5px; }
      background: $pinky;
      border: 2px solid $white;
      top: -2px;
      right: -2px;
      font-family: $roboto-light;
      font-size: 13px;
      @include mq(ip5) {
        font-size: 10px; }
      letter-spacing: -0.26px;
      line-height: (15/13);
      text-transform: uppercase; } }

  &--clear {
    @extend .info-box;
    background: rgba($white, 0.1);
    border: 1px solid rgba($white, 0.5);
    font-size: 18px;
    padding: 20px 18px;
    @include mq(s5) {
      padding: 15px 12px; }
    @include mq(ip5) {
      padding: 10px 9px; }

    &--downloads-style {
      @extend .info-box--clear;
      padding-right: 7px;
      margin: 10px 0 0; } }

  &--dark {
    @extend .info-box;
    background: rgba(0,0,0,0.4);
    border: 1px solid rgba($white, 0.6); }

  &--pinky {
    @extend .info-box;
    background: $pinky;
    border: 5px solid $white;
    border-radius: 10px;
    margin-bottom: 20px; }

  &--nature-relaxation {
    @extend .info-box;
    padding: 20px 20px;
    font-size: 18px;
    border-radius: 0;
    border-bottom: 1px solid rgba($white, 0.5);
    border-top: 1px solid rgba($white, 0.5);
    justify-content: flex-start;
    margin: 0 -20px;
    background-image: url('../img/bg-nature-relaxation.png');
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; }


  &--transparent {
    @extend .info-box;
    padding: 10px 0;
    font-size: 18px;
    border-radius: 0;
    border-bottom: 1px solid rgba($white, 0.2);
    justify-content: flex-start;
    margin: 0;
    &:active {
      opacity: 1; }

    &:last-child {
      border: none; }

    &--border-top {
      border-top: 1px solid rgba($white, 0.2); }

    &--no-border {
      border: none; }

    &--medium-padding {
      @extend .info-box--transparent;
      padding: 20px 0; }

    &--large-padding {
      padding: 30px 0; } }

  &__live {
    display: block;
    border-radius: 100px;
    border: 3px solid $white;
    background: $alert;
    padding: 3px;
    text-align: center;
    font-family: $roboto-medium;
    text-transform: uppercase;
    color: $white;
    font-size: 13px;
    letter-spacing: -0.26px;
    line-height: (18/13);
    width: 70px;
    margin-right: 15px;

    &--live {
      @extend .info-box__live;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 10px; }

    &--rsvp {
      @extend .info-box__live;
      background: $rsvp; }

    &--free {
      @extend .info-box__live;
      background: $free; }

    &--locked {
      @extend .info-box__live;
      background: $locked; }

    &--edit {
      @extend .info-box__live;
      background: $calm;
      height: 30px;
      margin-right: 0; } }

  .icon {
    min-width: 30px;
    max-width: 30px;
    height: 30px;
    margin: 0 10px;
    img {
      max-height: 100%; } }

  .corporate-logo {
    min-width: 70px;
    max-width: 70px;
    margin-right: 15px; }

  .rounded-icon {
    min-width: 70px;
    max-width: 70px;
    height: 70px;
    margin-right: 15px;
    &--large {
      min-width: 80px;
      max-width: 80px;
      height: 80px;
      margin-right: 10px;
      @include mq(s5) {
        min-width: 60px;
        max-width: 60px;
        height: 60px;
        margin-right: 15px; }
      @include mq(ip5) {
        min-width: 60px;
        max-width: 60px;
        height: 60px;
        margin-right: 15px; } }

    &--pinky-play {
      min-width: 55px;
      max-width: 55px;
      height: 55px;
      margin-right: 10px; }

    &--medium {
      min-width: 45px;
      max-width: 45px;
      height: 45px;
      margin-right: 10px;
      img {
        max-height: 100%; }
      @include mq(s5) {
        min-width: 35px;
        max-width: 35px;
        height: 35px; }
      @include mq(ip5) {
        min-width: 35px;
        max-width: 35px;
        height: 35px; } }
    &--small {
      min-width: 35px;
      max-width: 35px;
      height: 35px;
      margin-right: 10px; }

    &--normal-margin {
      margin-right: 15px !important; } }

  .checked {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: -5px;
      right: -5px;
      width: 30px;
      height: 30px;
      display: block;
      background: url('../img/ico-complete.png') no-repeat center / cover; } }

  .incomplete {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: -5px;
      right: -5px;
      width: 30px;
      height: 30px;
      display: block;
      background: url('../img/ico-incomplete.png') no-repeat center / cover; } }

  .dotted-line {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      display: block;
      height: 100px;
      width: 1px;
      border-right: 2px dotted $white;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translateX(-50%); } }

  .corporate-icon {
    min-width: 27px;
    max-width: 27px;
    height: 54px;
    margin-left: 10px;
    margin-right: 20px; } }
