.welzen-popup {
  position: absolute;
  transform-style: preserve-3d;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
  background: $calm;
  border: 2px solid $white;
  border-radius: 5px;
  width: 330px;
  color: $white;
  pointer-events: none;
  opacity: 0;
  transition: .5s .15s;
  @include mq(ip5) {
    width: 290px; }

  &.popup-active {
    z-index: 3;
    pointer-events: all;
    opacity: 1; }


  &__container {
    width: 100%;
    height: 100%;
    padding: 18px;
    position: relative; }

  &__close {
    position: absolute;
    width: 30px;
    height: 30px;
    top: -15px;
    right: -10px; }

  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    &__icon {
      min-width: 55px;
      max-width: 55px;
      height: 55px;
      margin-right: 15px;
      img {
        max-height: 100%; } } }

  &__title {
    display: block;
    font-family: $roboto-regular;
    font-size: 20px;
    letter-spacing: -0.4px;
    line-height: (24/20);
    margin-bottom: 15px;
    @include mq(ip5) {
      font-size: 17px;
      margin-bottom: 10px; } }

  &__contents {
    display: block;
    font-family: $roboto-regular;
    font-size: 16px;
    letter-spacing: -0.32px;
    line-height: (22/16);
    margin-bottom: 15px;
    @include mq(ip5) {
      font-size: 14px; } }

  &__separator {
    content: '';
    width: 100%;
    height: 1px;
    background: rgba($white, 0.5);
    margin-bottom: 15px; }

  &__double-btns {
    display: flex;
    justify-content: space-between; }

  &__cta {
    display: block;
    background: $grape;
    text-decoration: none;
    font-family: $roboto-regular;
    font-size: 22px;
    letter-spacing: -0.48px;
    line-height: (28/24);
    padding: 20px;
    color: $white;
    text-align: center;
    border-radius: 8px !important;
    text-transform: uppercase;
    flex: 1;
    &:active {
      opacity: 0.3; }
    &.activated {
      background: $grape !important;
      color: $white !important; }
    @include mq(ip5) {
      padding: 15px;
      font-size: 20px; } }


  &__mini-cta {
    display: block;
    background: $grape;
    text-decoration: none;
    font-family: $roboto-regular;
    font-size: 22px;
    letter-spacing: -0.48px;
    line-height: (28/24);
    padding: 20px;
    color: $white;
    text-align: center;
    border-radius: 8px !important;
    flex: 1;
    &:active {
      opacity: 0.3; }
    &.activated {
      background: $grape !important;
      color: $white !important; }
    @include mq(ip5) {
      padding: 15px;
      font-size: 20px; } }

  &__btn {
    display: block;
    background: rgba(0,0,0,0.1);
    text-decoration: none;
    font-family: $roboto-regular;
    font-size: 22px;
    letter-spacing: -0.48px;
    line-height: (28/24);
    padding: 20px;
    color: $white;
    text-align: center;
    border-radius: 8px !important;
    text-transform: uppercase;
    flex: 1;
    margin-right: 10px;
    &:active {
      opacity: 0.3; }
    &.activated {
      background: rgba(0,0,0,0.1) !important;
      color: $white !important; } }

  &__or {
    display: block;
    text-align: center;
    font-family: $roboto-light;
    font-size: 16px;
    letter-spacing: -0.32px;
    line-height: (19/16);
    margin: 10px 0;
    position: relative;

    &::before, &::after {
      content: '';
      position: absolute;
      z-index: -1; }

    &::before {
      width: 100%;
      height: 1px;
      background: rgba($white, 0.2);
      top: 50%;
      left: 0; }
    &::after {
      width: 25px;
      height: 25px;
      background: $calm;
      transform-style: preserve-3d;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); } }


  &__info-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: $rsvp;
    border: 2px solid $white;
    border-radius: 8px;
    padding: 8px 16px;
    color: $white;
    text-decoration: none;

    &__details {
      display: block;

      &--title {
        font-family: $roboto-regular;
        font-size: 22px;
        letter-spacing: -0.44px;
        line-height: (25/22);
        display: block; }

      &--brief {
        display: block;
        font-family: $roboto-regular;
        font-size: 13px;
        letter-spacing: -0.26px;
        line-height: (18/13); } }


    &__price {
      display: block;
      color: $white;
      font-family: $roboto-regular;
      font-size: 13px;
      letter-spacing: -0.26px;
      line-height: (18/13);
      background: $calm;
      border: 3px solid $white;
      border-radius: 100px;
      text-align: center;
      padding: 3px 16px; } }


  &__footer {
    display: block;
    text-align: center;
    font-family: $roboto-regular;
    font-size: 13px;
    letter-spacing: -0.26px;
    line-height: (18/13);
    margin-bottom: 10px;
    @include mq(ip5) {
      font-size: 12px; } }

  &__overlay {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.5);
    opacity: 0;
    transition: 0.3s 0.1s ease-in-out;
    pointer-events: none;

    &.popup-active {
      opacity: 1;
      z-index: 2;
      pointer-events: all; } } }
