input {
  width: 100%;
  padding: 10px 1px;
  border-bottom: 1px solid rgba($white, 0.5) !important;
  background: transparent;
  font-family: $roboto-light;
  color: $white !important;
  margin-bottom: 10px;
  border-radius: 0;

  &::-webkit-input-placeholder {
    color: $white; } }

form a {
  color: $white;
  font-family: $roboto-light;
  font-size: 16px;
  letter-spacing: -0.32px;
  line-height: 30/16;
  opacity: 0.5;
  text-align: right;
  display: block;
  margin-top: -5px;
  @include mq(ip5) {
    font-size: 14px; } }

form {
  margin: 0 0 20px 0; }

.form__info {
  display: block;
  color: $white;
  font-family: $roboto-light;
  font-size: 16px;
  letter-spacing: -0.32px;
  line-height: 30/16;
  opacity: 0.5;
  display: block;
  margin-top: -5px;
  text-align: left;
  @include mq(ip5) {
    font-size: 14px; } }


// Toggles (important is used for Android)
.item-toggle {
  background: transparent !important;
  color: $white;
  font-family: $roboto-regular;
  font-size: 18px;
  letter-spacing: -0.36px;
  line-height: (25/18);
  border: none;
  padding: 25px 0; }

.border-top {
  border-top: 1px solid rgba($white, 0.2); }

.border-bottom {
  border-bottom: 1px solid rgba($white, 0.2); }

.item-toggle .toggle {
  top: 25%;
  right: 0; }

.toggle .track {
  border: none !important;
  background: rgba($white, 0.6) !important;
  width: 51px !important;
  height: 31px !important; }

.toggle .handle {
  background: $locked;
  border: 2px solid $white;
  top: 7px !important;
  left: 7px !important;
  height: 27px !important;
  width: 27px !important; }

.toggle input:checked + .track .handle {
  background: $calm;
  transform: translate3d(20px, 0, 0) !important; }
