.welzenInput {
  display: block;
  position: relative;
  padding-top: 20px;

  .input-label {
    position: absolute;
    z-index: 2;
    transform: translate3d(0, -30px, 0) scale(1);
    transition: all 0.2s ease;
    color: $white;
    transform-origin: 0;
    font-family: $roboto-light;
    top: 90%;
    width: 100%; }

  input {
    height: auto;
    border: none; }

  input:focus ~ .input-label,
  input.used ~ .input-label {
    transform: translate3d(0, -60px, 0) scale(0.9);
    font-family: $roboto-regular; } }
